import React, { useState } from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import styled from "styled-components";
import { Link } from "gatsby";
import { MdPhoneInTalk } from "react-icons/md";
import FormModal from "@/components/FormModal";
import { MenuData, LogoImage } from "@/data";
import Button from "@/components/Button";

const StyledNavbar = styled(Navbar)`
  padding: 20px 0;
    z-index: 1;
    width: 100%;
  & .navbar-toggler[aria-expanded="true"] span:first-child {
    transform: rotate(45deg);
    position: relative;
    top: 6px;
    background-color: rgb(0, 0, 0);
  }

  & .navbar-toggler[aria-expanded="true"] span:last-child {
    transform: rotate(-45deg);
    position: relative;
    top: -5px;
    background-color: rgb(0, 0, 0);
  }
`;

const NavBarLogo = styled(Navbar.Brand)`
  font-weight: bold;

  & img{
    width: 150px;
  }

  @media (max-width: 991px) {
    order: 1;
  }

  & a {
    color: #000;
    font-size: 25px;
  }
`;

const NavBarToggle = styled(Navbar.Toggle)`
  @media (max-width: 991px) {
    order: 3;
  }
  border: 0;

  &:focus {
    box-shadow: none;
  }
`;

const StyledNav = styled(Nav)`
  & a {
    color: #000;
    margin-right: 20px;

    &:active {
      transform: scale(0.95);
      opacity: 0.6;
      transition: opacity 0.2s ease 0s, transform 0.2s ease 0s;
    }

    &:hover {
      transform: scale(0.95);
      opacity: 0.6;
      transition: opacity 0.2s ease 0s, transform 0.2s ease 0s;
    }

    @media (max-width: 991px) {
      text-align: center;
      margin-right: 0px;
      margin-bottom: 10px;
    }

    &:last-child {
      margin-right: 0;
    }
  }
`;

const NavButtonMain = styled.div`
  @media (max-width: 991px) {
    order: 2;
    flex: 0 0 50%;
    text-align: end;
  }
  @media (max-width: 767px) {
    flex: 0 0 25%;
  }

  & button {
    & .label {
      @media (max-width: 767px) {
        display: none;
      }
    }

    & .icon {
      display: none;
      @media (max-width: 767px) {
        display: block;
      }
    }
  }
`;

const NavBarCollapse = styled(Navbar.Collapse)`
  @media (max-width: 991px) {
    order: 4;
    padding-top: 15px;
  }
`;

const NavIcon = styled.span`
  width: 30px;
  height: 2px;
  background-color: rgb(0, 0, 0);
  display: flex;
  margin: 10px 0px;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  transition: all 0.3s ease 0s;
`;

const Header = (props) => {
  const [open, setOpen] = useState(false);
  const [modalShow, setModalShow] = useState(false);

  return (
    <StyledNavbar expand="lg">
      <Container>
        <NavBarLogo>
          <Link to="/">
            <img src={LogoImage.light} alt="" />
          </Link>
        </NavBarLogo>
        <NavBarToggle
          aria-controls="basic-navbar-nav"
          onClick={() => setOpen(!open)}
          aria-expanded={open}
        >
          <NavIcon />
          <NavIcon />
        </NavBarToggle>
        <NavBarCollapse id="basic-navbar-nav">
          <StyledNav className="mx-auto">
            {MenuData?.map((item, index) => {
              return (
                <>
                  <Link to={item.path}>{item.name}</Link>
                </>
              );
            })}
          </StyledNav>
        </NavBarCollapse>
        <NavButtonMain>
          <Button
            onClick={() => setModalShow(true)}
            Label="Let`s Talk"
            isIcon
            Component={<MdPhoneInTalk />}
          />
        </NavButtonMain>
      </Container>
      <FormModal show={modalShow} onHide={() => setModalShow(false)} />
    </StyledNavbar>
  );
};

export default Header;
