import React from "react";
import Layout from "@/components/Layout";
import { Section } from "@/components/Globalcomponent";
import styled from "styled-components";
import HomeBG from "@/images/bg.png";
import Slider from "@/components/Home/Slider.jsx";
import Services from "@/components/Home/Services.jsx";

const HomeSlider = styled(Section)`
  width: 100%;

  padding-bottom: 0;

  &:after {
    content: "";
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#c09fff6b),
      to(#c09fff91)
    ) !important;
    position: absolute;
    width: 100%;
    display: block;
    max-width: 500px;
    max-height: 500px;
    height: 100%;
    top: 0;
    border-radius: 100%;
    filter: blur(127px);
    z-index: -1;
    transform: translateY(-135px);
    left: -63px;

    @media (max-width: 991px) {
      display: none;
    }
  }

  &:before {
    content: "";
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#c09fff6b),
      to(#c09fff91)
    ) !important;
    position: absolute;
    width: 100%;
    display: block;
    max-width: 500px;
    max-height: 500px;
    height: 100%;
    bottom: 0;
    right: 0;
    border-radius: 100%;
    filter: blur(127px);
    z-index: -1;

    @media (max-width: 991px) {
      display: none;
    }
  }
`;

const Service = styled(Section)``;

function Home() {
  return (
    <Layout>
      <HomeSlider>
        <Slider />
      </HomeSlider>
      {/* <Service> */}
      {/* <Services /> */}
      {/* </Service> */}
    </Layout>
  );
}

export default Home;
