import { createGlobalStyle } from "styled-components";
import "bootstrap/dist/css/bootstrap.min.css"
import FuturaPT from "../assets/font/FuturaPTDemi.otf"
import FuturaPTBold from "../assets/font/FuturaPTDemi.otf"
import FuturaPTBook from "../assets/font/FuturaPTBook.otf"
import FuturaPTExtraBold from "../assets/font/FuturaPTExtraBold.otf"

export const GlobalStyle = createGlobalStyle`

@font-face {
    font-family: FuturaPT;
    src: url(${FuturaPT});
  }
  @font-face {
    font-family: FuturaPTBold;
    src: url(${FuturaPTBold});
  }
  @font-face {
    font-family: FuturaPTBook;
    src: url(${FuturaPTBook});
  }
  @font-face {
    font-family: FuturaPTExtraBold;
    src: url(${FuturaPTExtraBold});
  }

  body{
    font-family: 'FuturaPT', sans-serif;
   
  }
  a{
    text-decoration: none;
  }
  ul{
    margin: 0;
    padding: 0;
  }
  li{
    list-style: none;
    
  }
`;
