import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import styled from "styled-components";
import { Link } from "gatsby";
import {
  FaFacebookF,
  FaInstagram,
  FaLinkedin,
  FaTwitter,
  FaWhatsapp,
} from "react-icons/fa";
import { HiMailOpen } from "react-icons/hi";
import { TiLocation } from "react-icons/ti";

const TopHeader = () => {
  return (
    <>
      <TopBarArea>
        <Container>
          <Row className="align-items-center">
            <Col lg={9}>
              <InfoBox>
                <List>
                  <ul>
                    <li>
                      <a href="#">
                        <ListIcon>
                          <TiLocation />
                        </ListIcon>
                        <ListText>Surat-395006, Gujrat, India.</ListText>
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <ListIcon>
                          <HiMailOpen />
                        </ListIcon>
                        <ListText>Info@libertyworld.in</ListText>
                      </a>
                    </li>
                  </ul>
                  <SocialList>
                    <li>
                      <a href="#">
                        <FaFacebookF />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <FaTwitter />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <FaInstagram />
                      </a>
                    </li>
                    <li>
                      <a href="#">
                        <FaLinkedin />
                      </a>
                    </li>
                  </SocialList>
                </List>
              </InfoBox>
            </Col>
            <Col lg={3}>
              <TextRight>
                <Link to="/https://wa.link/cqntz6" target="_blank">
                  <StyledButton>
                    <Icon>
                      <FaWhatsapp />
                    </Icon>
                    <Text>whatsapp</Text>
                  </StyledButton>
                </Link>
              </TextRight>
            </Col>
          </Row>
        </Container>
      </TopBarArea>
    </>
  );
};

const TopBarArea = styled.div`
  background: #022147 none repeat scroll 0 0;
  padding: 15px 0;
  overflow: hidden;
  @media (max-width: 767px) {
    display: none;
  }
`;

const InfoBox = styled.div``;

const TextRight = styled.div`
  @media (max-width: 991px) {
    display: none;
  }

  & a {
    color: #fff;
  }
`;

const StyledButton = styled.div`
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 1;
  cursor: pointer;
  &:after {
    position: absolute;
    left: 0;
    top: -35px;
    content: "";
    height: 100px;
    width: 100%;
    background: #036f4f;
    z-index: -1;
    transform: skewX(-30deg);
    border-left: 3px solid #fff;
  }
`;
const Text = styled.div`
  margin-left: 10px;
`;
const Icon = styled.div`
  display: flex;
  align-items: center;
`;

const List = styled.div`
  display: flex;
  @media (max-width: 767px) {
    display: block;
  }
  & ul {
    display: flex;
    @media (max-width: 767px) {
      display: block;
    }
    & li {
      margin-right: 15px;
      & a {
        display: flex;
        color: #fff;
      }
    }
  }
`;
const ListIcon = styled.div`
  display: flex;
  margin-right: 5px;
  & svg {
    font-size: 20px;
  }
`;
const ListText = styled.div``;

const SocialList = styled.ul`
  align-items: center;
  margin-left: 30px;
  padding-left: 30px;
  position: relative;
  z-index: 1;
  &:after {
    position: absolute;
    left: 0;
    top: 6px;
    content: "";
    height: 20px;
    width: 1px;
    background: hsla(0, 0%, 100%, 0.3);
  }
  @media (max-width: 767px) {
    display: flex;
  }
`;

export default TopHeader;
