import React from "react";
import styled from "styled-components";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Container } from "react-bootstrap";
import { SliderData } from "@/data";
import Button from "@/components/Button";
import { BsChevronLeft, BsChevronRight } from "react-icons/bs";

const SliderMain = styled.div``;

const MainSlick = styled(Slider)`
  & .slick-prev {
    left: 25px;
    z-index: 1;
  }

  & .slick-next {
    right: 25px;
  }
`;

const ContentSlider = styled.div`
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
`;

const SliderNorm = styled.div`
  & button {
    z-index: 2;
  }

  & h6 {
    color: #fff;
    line-height: 170px;
    letter-spacing: 0px;
    font-weight: 400;
    margin-bottom: 20px;
    font-size: 160px;
    -webkit-text-stroke: 1px rgba(255, 255, 255, 0.9);
    -webkit-text-fill-color: transparent;

    @media (max-width: 767px) {
      line-height: 45px;
      font-size: 45px;
    }
  }

  & p {
    color: #fff;
    font-size: 30px;
    @media (max-width: 767px) {
      font-size: 20px;
    }
  }
  & h1 {
    color: #fff;
    @media (max-width: 767px) {
      font-size: 20px;
    }
  }
`;

const StyledNext = styled.div`
  & svg {
    width: 30px;
    height: 30px;
    fill: #fff;
  }
  &:before {
    display: none;
  }
`;

const StyledPrev = styled.div`
  & svg {
    width: 30px;
    height: 30px;
    fill: #fff;
  }
  &:before {
    display: none;
  }
  &:before {
    display: none;
  }
`;

const SliderButton = styled(Button)`
  border-color: transparent;
`;

const MainSlider = styled.div`
  height: calc(100vh - 153px);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding-bottom: 0;
`;

function NextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <StyledNext
      style={{ ...style, display: "block" }}
      onClick={onClick}
      className={className}
    >
      <BsChevronRight />
    </StyledNext>
  );
}

function PrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <StyledPrev
      className={className}
      style={{ ...style, display: "block" }}
      onClick={onClick}
    >
      <BsChevronLeft />
    </StyledPrev>
  );
}

const HomeSlider = () => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 650,
    slidesToShow: 1,
    slidesToScroll: 1,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    infinite: true,
    fade: !0,
    cssEase: "linear",
    autoplay: true,
    autoplaySpeed: 2000,
    draggable: false,

    responsive: [
      {
        breakpoint: 767,
        settings: {
          nextArrow: false,
          prevArrow: false,
          arrows: false,
        },
      },
    ],
  };

  return (
    <>
      <MainSlick {...settings}>
        {SliderData?.map((item, index) => {
          console.log("data", item);
          return (
            <>
              <SliderMain key={index + 1}>
                <MainSlider
                  style={{
                    backgroundImage: `linear-gradient(to right, #0000005a, #00000047) , url(${item.image})`,
                  }}
                >
                  <Container className="h-100">
                    <ContentSlider>
                      <SliderNorm>
                        <h6>{item.Subtitle}</h6>
                        <p>{item.content}</p>
                        <SliderButton Label={item.btnContent} />
                      </SliderNorm>
                    </ContentSlider>
                  </Container>
                </MainSlider>
              </SliderMain>
            </>
          );
        })}
      </MainSlick>
    </>
  );
};

export default HomeSlider;
