import React from "react";
import styled from "styled-components";
import { Container, Row, Col  } from "react-bootstrap";
import { ServiceData } from "@/data";
import { Link } from "gatsby"

const ServiceMain = styled.div``;

const SectionTitle = styled.div``;

const SectionHeading = styled.div`
  & h2 {
    font-size: 48px;
    display: inline-block;
    letter-spacing: -1.6px;
  }
`;

const DetailsCard = styled.div``

const ServiceIcon = styled.div``

const ServiceContent = styled.div``


const Services = () => {
  return (
    <>
      <ServiceMain>
        <Container>
          <SectionTitle>
            <Row className="justify-content-center">
              <Col lg={7} md={10} sm={12}>
                <SectionHeading className="text-center">
                  <h2>We provide the best digital services</h2>
                  <p className="mt-4">
                    Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                    Laborum obcaecati dignissimos quae quo ad iste ipsum
                    officiis deleniti asperiores sit.
                  </p>
                </SectionHeading>
              </Col>
            </Row>
            <Row>
              {/* {ServiceData?.map((data, index) => {
                return (
                  <>
                  
                    <Col lg={4} md={6} sm={12} key={index + 1}>
                        <DetailsCard>
                            <ServiceIcon>
                                {data.icons}
                            </ServiceIcon>
                            <ServiceContent>
                                <h3>{data.name}</h3>
                                <p>{data.content}</p>
                                <Link to={data.path}>Learn More</Link>
                            </ServiceContent>
                        </DetailsCard>
                    </Col>
                  </>
                );
              })} */}
            </Row>
          </SectionTitle>
        </Container>
      </ServiceMain>
    </>
  );
};

export default Services;
