import React from "react";
import { Button } from "react-bootstrap";
import styled from "styled-components";

const StyledButton = styled(Button)`
  background-color: #036f4f;
  position: relative;
  border-color: transparent;

  &:hover {
    background-color: #022147;
    border-color: transparent;
  }

  &:focus {
    box-shadow: none !important ;
    background-color: #022147;
  }

  &:active {
    box-shadow: none !important;
    background-color: #022147;
    transform: scale(0.95);
  }

  &:active::before {
    animation: none;
    background-size: 0;
  }

  &:before {
    position: absolute;
    content: "";
    left: -2em;
    right: -2em;
    top: -2em;
    bottom: -2em;
    transition: ease-in-out 0.5s;
    background-repeat: no-repeat;
    background-image: radial-gradient(
        circle,
        transparent 20%,
        #022147 20%,
        transparent 30%
      ),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, #022147 20%, transparent 20%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, #022147 20%, transparent 20%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, transparent 20%, #fff 20%, transparent 30%),
      radial-gradient(circle, transparent 20%, #fff 20%, transparent 30%),
      radial-gradient(circle, #fff 20%, transparent 20%),
      radial-gradient(circle, #fff 20%, transparent 20%),
      radial-gradient(circle, transparent 20%, #022147 20%, transparent 30%),
      radial-gradient(circle, #022147 20%, transparent 20%);
    background-size: 20% 22%, 15% 19%, 22% 21%, 10% 19%, 15% 14%, 11% 11%,
      18% 18%, 18% 20%, 14% 19%, 18% 14%, 18% 14%, 17% 14%, 11% 19%, 13% 15%,
      18% 12%, 19% 18%;
    background-position: 18% 40%, 20% 31%, 30% 30%, 40% 30%, 50% 30%, 57% 30%,
      65% 30%, 80% 32%, 15% 60%, 83% 60%, 18% 70%, 25% 70%, 41% 70%, 50% 70%,
      64% 70%, 80% 71%;
    animation: bubbles ease-in-out 0.75s forwards;
    pointer-events: none;
  }

  @keyframes bubbles {
    0% {
      background-position: 18% 40%, 20% 31%, 30% 30%, 40% 30%, 50% 30%, 57% 30%,
        65% 30%, 80% 32%, 15% 60%, 83% 60%, 18% 70%, 25% 70%, 41% 70%, 50% 70%,
        64% 70%, 80% 71%;
    }

    50% {
      background-position: 10% 44%, 0% 20%, 15% 5%, 30% 0%, 42% 0%, 62% -2%,
        75% 0%, 95% -2%, 0% 80%, 95% 55%, 7% 100%, 24% 100%, 41% 100%, 55% 95%,
        68% 96%, 95% 100%;
    }

    100% {
      background-size: 0% 0%;
    }
  }
`;

const ButtonLabel = styled.div``;

const ButtonIcon = styled.div``;

const ButtonShare = ({ Label, onClick, isIcon, Component }) => {
  return (
    <StyledButton onClick={onClick}>
      <ButtonLabel className="label">{Label}</ButtonLabel>

      {isIcon && (
        <>
          <ButtonIcon className="icon">{Component}</ButtonIcon>
        </>
      )}
    </StyledButton>
  );
};

export default ButtonShare;
